export function initializeRating () {
  if (window.AppRate) {
    /* eslint no-undef: ["error", { "typeof": true }] */
    AppRate.setPreferences({
      storeAppURL: {
        android: 'https://market.android.com/details?id=com.libreappfoundation.mobiletv'
      },
      callbacks: {
        onButtonClicked: function (buttonIndex) {
          AppRate.promptForRating(false)
        }
      },
      useLanguage: 'en',
      simpleMode: true,
      openUrl: function (url) {
        window.cordova.plugins.market.open('com.libreappfoundation.mobiletv')
      }
    })
    AppRate.promptForRating(false)
  }
}

export function openRateDialog () {
  if (window.AppRate) {
    /* global AppRate */
    /* eslint no-undef: ["error", { "typeof": true }] */
    AppRate.promptForRating()
  }
}



